<script>
export default {
  data() {
    return{
      info:[
        ["AB","2"],
        ["BC","8"],
        ["CD","2"],
        ["AD","8"],
        ["4 Lados","20"],
      ],
      section3:{
        src:'', 
        file:''
      },
      photoPlaceholder: require("@/assets/images/activities-assets/activity_1-1/Recurso 54@4x.png"),
    }
  },
  methods: {
    uploadImage(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      this.section3.file = file;
      reader.onload = (e) => {
        this.section3.src = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  }
}
</script>

